
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
components: {
Popup
},
data () {
return {
modalShow: false
}
},
methods: {
show () {
this.modalShow = true
},
close () {
this.modalShow = false
}
}
}




