
import { mapState } from 'vuex'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'app',
components: {
Loading
},
mounted(){
this.$store.dispatch('getJssdkConfig',this.$wechat)
} ,
computed: {
...mapState({
direction: state => state.direction.direction
}),
isLoading: {
get: function () {
return this.$store.state.isLoading
},
set: function(newVal) {
this.$store.state.isLoading = newVal
}
}
}
}



