import DataScroll from './directive';

const install = function(Vue) {
  Vue.directive('DataScroll', DataScroll);//添加全局资源
};

if (window.Vue) {
  window.dataScroll = DataScroll;
  Vue.use(install); // eslint-disable-line
}

DataScroll.install = install;
export default DataScroll;//Vue.use(MyPlugin)调用插件


