
import Vue from 'vue'
import moment from 'moment'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import globals from '@/libs/globals.mixin'
import MyRater from 'c%/MyRater.vue'
import Voice from 'c%/playVoice.vue'
import VueVideo from 'c%/vueVideo'
import shareModal from 'c%/ShareModal.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default{
mixins: [common,wxjssdk,globals],
components: {
Popup,
Rater,
Voice,
MyRater,
Flexbox,
XButton,
shareModal,
FlexboxItem
},
data () {
return {
info: {},
active_voice: -1,
ready: false,
source: [],
popup_video: false,
mapContent: {'lesson_after_task':'课后作业','lesson_content':'上课内容','next_task':'下堂课内容'},
}
},
mounted () {
this.init()
},
watch: {
ready: function (val) {
if(!val) {
this.showLoading()
}else{
this.hideLoading()
}
}
},
computed: {
sheduleDate () {
let result = ''
if(this.info.int_day){
result = moment(this.$filter('int_date')(this.info.int_day)).format('YYYY年MM月DD日')
}
return result
},
activeStar () {
return `<img slot="active" src="/static/img/student/star-sel.png" style="width:20px">`
},
defaultStar () {
return `<img slot="default" src="/static/img/student/star-nor.png" style="width:20px">`
},
currentTpl () {
let review = this.info.review,
result = {}
if(review){
result = review.review_tpl_setting
}

return result
},
content_fields () {
let result = {},
fields = this.currentTpl.setting?this.currentTpl.setting.common_fields:{}
for(let s in fields){
let obj = fields[s]
if(obj.enable==1&&obj.student_view==1){
result[s] = {}
result[s].label = obj.label
result[s].value = obj.tpl
}
}

return result
},
content_value () {
let result = {}
if(!_.isEmpty(this.info)){
if(this.info.review){
result = JSON.parse(this.info.review.content)
}
}
return result
},
scores () {
let result = {},
fields = this.currentTpl.setting?this.currentTpl.setting.score_fields:{}

return fields
}
},
methods: {
getVOptions (row){
return {
autoplay: false,
volume: 0.6,
poster: row.file_url+'?vframe/jpg/offset/7/w/130/h/130'
}
},
preview_video (row) {
let obj = util.copy(row)
obj.src = row.file_url
this.source = [obj]
this.popup_video = true
},
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
},
hideLoading () {
setTimeout(()=>{
this.$vux.loading.hide()
},300)
},
init () {
this.showLoading()
this.init_data()
},
init_data() {
this.ready = false
this.$rest('share/my_review_detail?rs_id='+this.$route.params.id)
.get()
.success(data=>{
this.info = data
this.ready = true
})
},
setShare () {
let [title,img,desc] = [this.info.student.student_name+'的个人点评','',this.info.detail],
imgList = this.imglist()

if(imgList.length>0){
img = imgList[0]
}

this.$refs['share'].show()

this.setWxShare(title,img,desc)
.then(()=>{

this.share()
},()=>{})
},
share () {
this.$rest('share/after_share_review?rs_id='+this.$route.params.id)
.get()
.success(data=>{
this.alert('分享成功','分享成功',true)
}).error(response=>{
console.log('error')
console.log(response)
this.alert('分享失败',response.body.message||'分享失败了')
})
},
labelScore (index) {
let scoreFields = this.scores
if(!_.isEmpty(scoreFields)){
return scoreFields['score'+(index>0?index:'')].label
}
return ''
},
file_list (type) {
let [result,review] = [[],this.info.review]
if(review&&review.review_file.length>0){
result = review.review_file.filter(u=>u.file_type==type)
}
return result
},
imglist () {
let list = this.file_list('image'),
result = []
if(list.length>0){
list.forEach(l=>{
result.push(l.file_url)
})
}
return result
}
}
}



