
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import MyPopupPicker from 'c%/PopupPicker'
import signupSuccess from './signup-success.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


const emptyObject = {
student_name: '',
first_tel: '',
sex: ['男'],
birth_time: '',
first_family_rel: ['自己'],
first_family_name: '',
school_id: '',
school_class: '',
school_grade: 0
}

export default{
mixins: [common],
components: {
Group,
XInput,
XButton,
Datetime,
PopupPicker,
signupSuccess,
MyPopupPicker,
},
data () {
return {
info: util.copy(emptyObject),
logo: '/static/img/student/logo-2.jpg',
sexList: [['男','女']],
relList: [['自己','爸爸','妈妈','其他']],
show: true
}
},
computed: {
maxYear () {
return parseInt(moment(new Date()).format('YYYY'))
},
schools () {
let public_schools = this.$store.state.gvars.public_schools,
result = []
if(public_schools&&public_schools.length){
public_schools.forEach(s=>{
result.push({
key: s.ps_id,
value: s.school_name
})
})
}
return result
},
grades () {
let grades = this.dicts('grade'),
result = []
if(grades.length>0){
grades.forEach(g=>{
result.push({
key: parseInt(g.name),
value: g.title
})
})
}
return result
}
},
mounted () {
this.init_dict()
},
methods: {
select_school (school_id) {
console.log('school_id:'+school_id)
this.info.school_id = school_id
},
init_dict () {
this.$rest('open/global/name=dicts').get()
.success(data=>{
this.$store.commit('setGlobalVars',data)
})
},
check () {
let info = this.info,
content = '',
result = true

if(!/^1[35789]{1}\d{9}$/.test(info.first_tel)){
content = '手机号格式错误'
result = false
}
if(/^\s*$/.test(info.first_tel)){
content = '请输入学员手机号'
result = false
}
if(/^\s*$/.test(info.student_name)){
content = '请输入学员姓名'
result = false
}
if(content!=''){
this.alert('提示',content)
}

return result
},
deal_param () {
let info = this.info,
params = util.copy(info)
params.sex = this.sexList[0].indexOf(info.sex[0]) + 1
params.first_family_rel = this.relList[0].indexOf(info.first_family_rel[0]) + 1

return params
},
ok () {
this.show = false
this.$refs['success'].showSuccess()











}
}
}



