import {_} from './util'
import moment from 'moment'

function int_day_to_date(int_day){
	let int_day_str = int_day.toString();
    let y = parseInt(int_day_str.substr(0,4),10),
        m = parseInt(int_day_str.substr(4,2),10)-1,
        d = parseInt(int_day_str.substr(6,2),10),
        dt = new Date(y,m,d)
        dt.setHours(0,0,0,0)
    return dt;
}

function broadcast(componentName, eventName, params) {
    this.$children.forEach(child => {
        const name = child.$options.name;

        if (name === componentName) {
            child.$emit.apply(child, [eventName].concat(params));
        } else {
            // todo 如果 params 是空数组，接收到的会是 undefined
            broadcast.apply(child, [componentName, eventName].concat([params]));
        }
    });
}

export default {
    data(){
        return {
            action :'index',
            error:'',
            msg:{
                title:'操作提示',
                description:'操作描述',
                buttons:[],
                icon:'success' //success, warn, info, waiting
            },
            pageIndex: 1,
            pageSize: 10,
            busy: false,
            loading: false,
            isloadAll: false,
            uploadList: [],
            mescroll: null
        }
    },
	filters:{
		int_date(day_int){
			let s = day_int.toString(),
                t = [],
                l = s.length;
            if (l == 8) {
                t.push(s.substr(0, 4));
                t.push(s.substr(4, 2));
                t.push(s.substr(6, 2));
            } else if (l == 6) {
                for (let i = 0; i < l; i++) {
                    t.push(s.substr(i, 2));
                    i++;
                }
            }
            return t.join('-');
		},
		int_week(day_int){
			let t = ['日', '一', '二', '三', '四', '五', '六'];
            let dt = int_day_to_date(day_int);
            return t[dt.getDay()];
		},
		int_hour(hour_int){
			let s = hour_int.toString(),
                t = [],
                l = s.length;
            switch(l){
                case 0:
                    s = '0000';
                    break;
                case 1:
                    s = '000'+s;
                    break;
                case 2:
                    s = '00'+s;
                    break;
                case 3:
                    s = '0'+s;
                    break;
                default:
                    s = s;
            }            
           	t.push(s.substr(0,2))
           	t.push(s.substr(2,2))
            return t.join(':');
		}
	},
    methods: {
    	dr(){
            return this.$store.state.layout.$refs['dr']
        },
        page(action){
            this.action = action
        },
        _msg(title,content,icon,first_btn,second_btn){
            this.msg.title = title
            this.msg.description = content
            this.msg.icon = icon
            if(_.isFunction(first_btn)){
                this.msg.buttons[0] = {
                    type:'primary',
                    text:'确定',
                    onClick:first_btn
                }
            }else{
                this.msg.buttons[0] = first_btn
            }
           
            if(second_btn){
                if(_.isFunction(second_btn)){
                    this.msg.buttons[1] = {
                        type:'primary',
                        text:'返回',
                        onClick:second_btn
                    }
                }else{
                    this.msg.buttons[1] = second_btn 
                }
               
            }
            this.page('msg')
        },
        success(title,content,first_btn,second_btn){
            this._msg(title,content,'success',first_btn,second_btn)
        },
        error_msg(title,content,first_btn,second_btn){
            this._msg(title,content,'warn',first_btn,second_btn)
        },
        error_tip(tip){
            this.error = tip
        },
        clear_error(){
            this.error = ''
        },
        dispatch(componentName, eventName, params) {
            let parent = this.$parent || this.$root;
            let name = parent.$options.name;
            while (parent && (!name || name !== componentName)) {
                parent = parent.$parent;

                if (parent) {
                    name = parent.$options.name;
                }
            }
            if (parent) {
                parent.$emit.apply(parent, [eventName].concat(params));
            }
        },
        broadcast(componentName, eventName, params) {
            broadcast.call(this, componentName, eventName, params);
        },
        sapi (method,uri,params,loading) {
        	return new Promise((resolve,reject)=>{
        		if(loading){
        			this.$store.commit('')
        		}
        		this.$http[method](uri,params)
        		.then(response=>{
        			resolve(response)        			
        		}).catch(response=>{        
        			reject(response)
        		})
        	})
        },
        dicts (type) {
        	let gvars = this.$store.state.gvars
        	if(gvars.dicts){
        		let dicts = gvars.dicts[type]
        		if(dicts&&dicts.length>0){
	        		return dicts
	        	}
        	}        	
        	return []
        },
        downCallback () {
			this.pageIndex = 0
			this.mescroll.resetUpScroll()
		},
		upCallback () {
            let get_param = {}
        	this.pageIndex++
            get_param.page = this.pageIndex
            get_param.pagesize = this.pageSize
            this.$http.get(this.data_uri,{params:get_param})
            .then(response=>{	                
                this.deal_data(response)     	
            }).catch(response=>{
            	this.mescroll.endErr()
            	this.toast(response.body.message,'text')
            })
        },
        deal_data (response) {
        	let list = response.body.data.list,	
            	total = response.body.data.total,
            	curLength = 0
            	
        	if(this.pageIndex===1){
        		this.data = []
        	}        	
    		this.data = this.data.concat(list)
    		
    		this.mescroll.endBySize(list.length, total)
        },
        loadMore () {
        	if(this.isloadAll){ return false }
            let get_param = {}
        	this.pageIndex++
            get_param.page = this.pageIndex
            get_param.pagesize = this.pageSize
            this.loading = true
            this.busy = true
            this.$http.get(this.data_uri,{params:get_param})
            .then(response=>{
                let list = response.body.data.list
                if(list.length == 0 || list.length < this.pageSize){
                    this.isloadAll = true
                }
                this.data = this.data.concat(list)
            	setTimeout(()=>{
            		this.loading = false
            		this.busy = false	
            	},1000)            	
            }).catch(response=>{
            	this.loading = false
            	this.toast(repsonse.body.message,'text')
            })
        },
        getAge (birth_time) {
			return parseInt(moment(new Date()).format('YYYY')) - parseInt(moment(birth_time).format('YYYY')) + '岁'
		},
        toast (msg,type) {
        	//type : success, warn, cancel, text
        	this.$vux.toast.show({
        	  type: type || 'text',
			  text: msg
			})
        },
        alert (title,msg,autoHidden) {
        	return new Promise((resolve,reject)=>{
        		this.$vux.alert.show({
				  title: title || '操作提示',
				  content: msg,
				  onHide() {
				  	resolve()
				  }
				})
		    	if(autoHidden){
		    		setTimeout(()=>{
						this.$vux.alert.hide()
						resolve()
					},3000)
		    	}
        	})	    	
        },
        showLoading () {
	        this.$vux.loading.show({
	          text: 'Loading'
	        })	        
	    },
	    hideLoading () {
	    	setTimeout(() => {
	          this.$vux.loading.hide()
	        }, 800)
	    },
        handleRemove (file) {
            // 从 upload 实例删除数据
            const fileList = this.uploadList;
            this.uploadList.splice(fileList.indexOf(file), 1);
        },
        handleSuccess (res, file) {
        	let data = res.data
        	data.url = data.file_url
            // 上传成功   添加到list
            this.uploadList.push(res.data)
            this.hideLoading()
        },
        handleFormatError (file) {
        	this.hideLoading()
            this.alert(
                '文件格式不正确',
                '文件 ' + file.name + ' 格式不正确，请上传 jpg 或 png 格式的图片。',
                true
            );            
        },
        handleMaxSize (file) { 
        	this.hideLoading()
            this.alert(
                '超出文件大小限制',
                '文件 ' + file.name + ' 太大，不能超过 2M。',
                true
            );
        },
        handleBeforeUpload (postFiles,file) {
            const check = (postFiles.length + this.uploadList.length) < 10;
            if (!check) {                	
            	this.hideLoading()
                this.alert(
                	'上传错误',
                	'最多只能上传 9  张图片。',
                	true
                );
            }else{
            	this.showLoading()
            }
            return check;
        },
    },
    computed: {
    	showHeader () {
	  		const ua = navigator.userAgent.toLowerCase()
		    if(ua.match(/MicroMessenger/i)=="micromessenger") {  
		        return false
		    }   
	        return true
	  	},  
    	headerHeight () {
	  		if(this.showHeader){
	  			return 46
	  		}
	  		return 0
	  	},
    	sid$ () {
    		return this.$store.state.user.info.default_sid
    	},
    	student$ () {
    		let students = this.$store.state.user.info.students,
    			sid = this.sid$,
    			result = {}
    		if(students&&students.length>0&&sid>0){
    			let student = students.find(s=>s.sid==sid)
    			if(student){
    				result = student
    			}
    		}
    		
    		return result
    	}
    }
}


