
import {_} from '@/libs/util'
import util from '@/libs/util'
import $rest from '@/libs/rest'
import store from '@/store'
import LoadMore from 'vux/src/components/load-more/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'



const DC = {}


function cache_key(url){
return util.sprintf('%s',url)
}

function http_get_promise(url){
return new Promise((resolve,reject)=>{
let k = cache_key(url)
if(!_.isUndefined(DC[k])){
resolve(DC[k])
}else{
$rest(url).get()
.success(response=>{
DC[k] = response
resolve(response)
})
.error(response=>{
reject(response)
})
}

})
}

export default{
name:'DataReady',
components: {
LoadMore
},
directives: {
TransferDom
},
props:{
data: {
type: Array,
default () {
return [];
}
}
},
mounted(){
this.init_data()
},
data(){
return {
ready:false
}
},
computed:{
loading(){
return this.ready === false
}
},
watch:{
data(){
this.init_data()
}
},
methods:{
init_data(){

if(this.data.length == 0){
this.ready = true
return
}
let promise_list = []
this.data.forEach(item=>{
if(_.isString(item)){
if(item == ''){
promise_list.push(item)
}else{
promise_list.push(http_get_promise(item))
}

}else if(_.isFunction(item)){
promise_list.push(item())
}else{
promise_list.push(item)
}
})

Promise.all(promise_list)
.then(result=>{
this.$emit('ready',result)
this.ready = true
},error=>{
this.$emit('error',error)
})
},
getData(url){
return http_get_promise(url)
},
refreshData(url){
return new Promise((resolve,reject)=>{
let k = cache_key(url)
$rest(url).get()
.success(response=>{
DC[k] = response
resolve(response)
this.$emit('refresh',{url,response})
})
.error(response=>{
reject(response)
})
})
}
}
}



