import GenericTransition from '../../common/generic-transition'
import PACKAGE_COMPONENT_PREFIX from '../../common/config'

let single = new GenericTransition(PACKAGE_COMPONENT_PREFIX + 'rotate-in-up-left', 'rotateInUpLeft')
let group = new GenericTransition(PACKAGE_COMPONENT_PREFIX + 'group-rotate-in-up-left', 'rotateInUpLeft', undefined, true)

export default { single, group } 



