export default{
	props: {
		src: {
			type: String,
			default: ''
		},
		active: {
			type: Boolean,
			default: false
		}	//控制播放、暂停
	},
	data () {
		return {
			isPlaying: false,
			duration: 0,
		}
	},
	watch: {
		active: function (val) {
			if(val){
				this.play()
			}else{
				this.ended()
			}				
		}
	},
	created () {
		this.get_duration()
	},
	methods: {
		togglePlay () {
			this.isPlaying = !this.isPlaying
			if(this.isPlaying){
				this.play()
			}else{
				this.stop()
			}
		},
		play () {
			this.isPlaying
            this.$refs.player.play()
	    },
	    stop () {
            this.$refs.player.pause()
	    },
	    ended () {
	    	this.$refs.player.currentTime = 0
	    	this.isPlaying = false
	    	this.$refs.player.pause()
	    },
	    get_duration () {
	    	let uri = 'open/voice_duration?url='+this.src+'?avinfo'
			this.$http.get(uri)
			.then(response=>{					
				this.duration = Math.floor(response.body.data.format.duration)
			},response=>{
				console.log(response.body.message||'获取mp3时长错误')
			})				
		}
	},
	computed: {
		
	}
}



