
import Vue from 'vue'
import con from '../const.js'
import Msg from 'vux/src/components/msg/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

export default{
name:'ChatUpload',
components: {
Msg,
XButton,
Popup,
Spinner
},
data(){
return {
buttons2:[{
type:'primary',
text:'确定',
onClick:this.cancelUpload.bind(this)
}]
}
},
created(){
let chat_upload = Vue.localStorage.get(con.CU_KEY)
if(chat_upload){
chat_upload = JSON.parse(chat_upload)
if(chat_upload.path != ''){
this.$router.replace(chat_upload.path)

}
this.bind_socket_event()
setTimeout(()=>{
this.$store.commit('recoverChatUpload',chat_upload)
},1000)
}
},
watch:{
'$store.state.chat_upload.popup':function(popup){
if(popup){
this.bind_socket_event()
}
}
},
methods:{
startUpload(){
this.$store.commit('startChatUpload')
this.$wechat.closeWindow()
},
cancelUpload(){
this.$store.commit('closeChatUpload')
},
bind_socket_event(){
if(!this.$options.sockets.onmessage){
this.$options.sockets.onmessage = (event)=>{
let data = JSON.parse(event.data)
if(data.event == 'push_media:status'){
this.push_media_status(data.data)
}
}
}
},
push_media_status(session){
let status = session.status
this.$store.commit('updateChatUpload',session)

},
},
computed:{
status(){
return this.$store.state.chat_upload.status
},
fileInfo(){
return '文件路径:<a href="'+this.$store.state.chat_upload.data.file_url+'">'+this.$store.state.chat_upload.data.file_url+'</a>'
},
started(){
return this.$store.state.chat_upload.started
}
}
}



