import Layout from './views/layout.vue'
import PageLogin from './views/login.vue'
import PageLogout from './views/logout.vue'
import PageForgotPwd from './views/forgotpwd.vue'
import PageRegister from './views/register.vue'
import PageWxtoken from './views/wxtoken.vue'
import Signup from './views/public/signup.vue'
import Comment from './views/public/comment.vue'
import TeacherIntroduce from './views/public/teacher-introduce.vue'

const routers = [
	{
	    path: '/login',
	    component: PageLogin,
	    name: 'login',
	    meta:{
	    	public:true,
	    	title:'登录'
	    }
    },
    {
        path: '/logout',
        component: PageLogout,
        name: 'logout',
        meta:{
            public:true,
            title:'退出登录'
        }
    },
    {
        path:'/forgotpwd',
        component:PageForgotPwd,
        name:'forgotpwd',
        meta:{
            public:true,
            title:'忘记密码'
        }
    },
    {
    	path:'/register',
    	component:PageRegister,
    	name:'register',
    	meta:{
    		public:true,
    		title:'注册'
    	}
    },
    {
        path:'/wxtoken',
        component:PageWxtoken,
        name:'wxtoken',
        meta:{
            
        }
    },
    {
    	path:'/signup',
    	component:Signup,
    	name:'signup',
    	meta:{
    		public:true,
    		show_share: true,
    		title:'报名信息登记'
    	}
    },    
    {
    	path: '/teacher/:id',
    	component: TeacherIntroduce,
    	name: 'introduce',
    	meta: {
    		public: true,
    		show_share: true,
    		title: '教师详情'
    	}
    },
    {
    	path: '/reviews/:id',
    	component: Comment,
    	name: 'review',
    	meta: {
    		public: true,
    		show_share: true,
    		title: '课评-详情',
    	}
    },
    {
        path: '*',
        meta:{
			
        },
        hidden: true,
        redirect: { path: '/home' }
    },
	{
	    path: '/',
		component: Layout,
		redirect:'/index',
		children:[
			//首页
            {
                path: 'index',
                component: resolve => require(['./views/home/index.vue'], resolve),
                redirect: '/home',
                meta: {
                    title: "首页",
                    show_footer: true,
                },
            },
            {
                path: 'home',
                component: resolve => require(['./views/home/home.vue'], resolve),
                meta: {
                    title: "首页",
                    show_footer: true
                }                
            },
        	{
        		path:'schedules',
        		component:resolve=>require(['./views/home/schedules.vue'],resolve),
        		meta:{
        			title:'课表'
        		}
        	},
        	{
        		path:'schedules/:id',
        		component:resolve=>require(['./views/home/schedule/detail.vue'],resolve),
        		meta:{
        			title:'课表详情'
        		}
        	},
        	{
        		path:'attendances',
        		component:resolve=>require(['./views/home/attendances.vue'],resolve),
        		meta:{
        			title:'考勤'
        		}
        	},
        	{
        		path:'reviews',
        		component:resolve=>require(['./views/home/reviews.vue'],resolve),
        		meta:{
        			title:'点评'
        		}
        	},
        	{
        		path:'reviews/:id',
        		component:resolve=>require(['./views/home/review/detail.vue'],resolve),
        		meta:{
        			title:'点评详情'
        		}
        	},
        	{
        		path:'news',
        		component:resolve=>require(['./views/home/news.vue'],resolve),
        		meta:{
        			title:'通知'
        		}
        	},
        	{
        		path:'news/:id',
        		component:resolve=>require(['./views/home/new/detail.vue'],resolve),
        		meta:{
        			title:'通知详情'
        		}
        	},
            {
                path:'lesson',
                component:resolve => require(['./views/lesson/index.vue'],resolve),
                redirect:'/lesson/index',
                meta:{
                    title:'课程'
                },
                children: [
                	{
				    	path: 'index',
				    	component:resolve=>require(['./views/lesson/lesson.vue'],resolve),
				    	meta:{
				    		title:'课程',
				    		show_footer: true
				    	}
				    },
				    {
				    	path: 'mylesson',
				    	component:resolve=>require(['./views/lesson/mylesson.vue'],resolve),
				    	meta: {
				    		title: '我的课程',
				    	}
				    },
				    {
				    	path: 'class',
				    	component:resolve=>require(['./views/lesson/class.vue'],resolve),
				    	meta: {
				    		title: '我的班级',
				    	}
				    }
                ]
            },
            {
                path:'my',
                component:resolve => require(['./views/my/index.vue'],resolve),
                redirect:'/my/index',
                meta:{
                    title:'我的'
                },
                children: [
                	{
						path: 'index',
						component:resolve => require(['./views/my/my.vue'],resolve),
						meta: {
							title: '我的',
							show_footer: true,
						}
					},
                	{
						path: 'archives',
						component:resolve => require(['./views/my/archives.vue'],resolve),
						meta: {
							title: '我的档案',
							show_footer: true,
						}
					},
					{
						path: 'add',
						component:resolve => require(['./views/my/add.vue'],resolve),
						meta: {
							title: '添加档案'
						}
					},
			
					{
						path: 'message',
						component:resolve => require(['./views/my/message.vue'],resolve),
						meta: {
							title: '我的消息',
						}
					},	
					{
						path: 'wallet',
						component:resolve => require(['./views/my/wallet.vue'],resolve),
						meta: {
							title: '电子钱包',
						}
					},	
					{
						path: 'credit',
						component:resolve => require(['./views/my/credit.vue'],resolve),
						meta: {
							title: '我的积分',
						}
					},
					{
						path: 'orders',
						component:resolve => require(['./views/my/orders.vue'],resolve),
						meta: {
							title: '我的订单',
						}
					},	
					{
						path: 'orders/:id',
						component:resolve => require(['./views/my/order/detail.vue'],resolve),
						meta: {
							title: '订单详情',
						}
					},
					{
						path: 'payments',
						component:resolve => require(['./views/my/payments.vue'],resolve),
						meta: {
							title: '缴费记录',
						}
					},	
					{
						path:'complaints',
						component:resolve => require(['./views/my/complaints.vue'],resolve),
						meta:{
							title:'投诉建议'
						}
					},	
					{
						path:'complaints_record',
						component:resolve => require(['./views/my/complaints-record.vue'],resolve),
						meta:{
							title:'历史记录'
						}
					},	
					{
						path: 'setting',
						component:resolve => require(['./views/my/setting.vue'],resolve),
						meta: {
							title: '设置',
						}
					}
                ]
            },
		]

	}

]


export default routers


