
import { mapState } from 'vuex'
import ChatUpload from 'c%/ChatUpload.vue'
import DataReady from 'c%/DataReady.vue'
import common from '@/libs/common.mixin'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

const footer_navs = [
{
path: '/home',
icon: 'home',
label: '首页'
},
{
path: '/lesson',
icon: 'lesson',
label: '课程'
},





{
path: '/my',
icon: 'mine',
label: '我的'
},
]
export default {
mixins: [common],
components: {
Tabbar,
ViewBox,
XHeader,
DataReady,
TabbarItem,
ChatUpload
},
data () {
return {
navs: footer_navs,
gurls:['global/all']
}
},
created(){
this.$store.commit('setLayout',this)
},
methods: {
dataReady(rs){
this.$store.commit('setGlobalVars',rs[0])
},
},
computed: {
...mapState({
direction: state => state.direction.direction,
show_footer: state => state.show_footer
}),
title () {
return this.$route.meta.title
}
}
}



